@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

@media only screen and (max-width: 400px) {
  header {
    height: 100px !important;
  }

  main {
    padding-top: 100px !important;
    /* header 50px height*/
  }
}

/* h2 is About */
h2 {
  text-align: left;
  font-family: Verdana, Tahoma;
  letter-spacing: 1.5px;
  border-bottom: 4px solid #dfdfe2;
  max-width: 80%;
  margin-top: 0px;
  padding-top: 10px;
}

h2::before {
  content: "//";
}

.info {
  padding: 0.5px;
}

/* About */

main {
  padding-top: 50px;
  /* header 50px height*/
}

body {
  text-align: center;
  align-items: center;
  background: #1b1b32;
  color: #efefef;
  font-family: Helvetica;
  margin: 0;
}

body a {
  color: #efefef;
  text-decoration: none !important;
}

body a:hover {
  text-decoration: underline;
  background-color: #f1be32;
  color: #1b1b32;
}

.tableOutline {
  width: 90%;
  max-width: 850px;
  max-height: 280px;
  aspect-ratio: 3 / 1;
  margin-left: auto;
  margin-right: auto;
  min-height: ;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #9bbc0f;
  color: #1b1b32;
  border: 5px outset #272929;
  border-radius: 5px;
}

.definition {
  max-width: 850px;
  width: 85%;
  text-align: justify;
  padding: 3%;
  margin-left: auto;
  margin-right: auto;
  background-color: #aaaaaa;
  color: #1b1b32;
  border-radius: 15px;
}

.output {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  align-content: center;
  font-weight: bold;
  font-size: 1.2rem;
}

#text::before {
  content: '“';
  font-size: 1.4rem;
}

#text::after {
  content: '”';
  font-size: 1.4rem;
}

#author {
  text-align: right;
  font-style: italic;
}

#author::before {
  content: ' - ';
}

#new-quote {
  transition-duration: 0.4s;
  width: 140%;
  height: 70px;
  border: 1.5px solid black;
  border-radius: 50px;
  padding: 14px 28px;
  font-size: 1em;
  cursor: pointer;
  text-align: center;
  transform: rotate(0.1deg);
  margin: auto;
}

#new-quote:hover {
  text-decoration: underline;
}

.fa {
  padding: 20px;
  font-size: 30px;
  width: 30px;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  border: 1.5px solid black;
  margin: auto;
}

#tweet-quote .fa:hover {
  background-color: #55ACEE !important;
  text-decoration: nome;
  color: none;
}

.fa-twitter {
  background: #55ACEE !important;
  color: white;
}

.flex-container {
  margin: auto;
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.flex-container a {
  background: none !important;
}